




























import Vue from 'vue';
import { WorkspaceFolderModel, WorkspaceObjectModel, WorkspaceObjectType } from '@/apis/models/WorkspaceObjectModel';
import WorkspaceObjectItem from '@/components/test_maker/WorkspaceObjectItem.vue';

export default Vue.extend({
  components: {
    WorkspaceObjectItem,
  },
  props: {
    selection: {
      type: Array as () => Array<WorkspaceObjectModel>,
      required: true,
    },
    selectModeOpen: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
    };
  },
  computed: {
    currentFolder(): WorkspaceFolderModel | null {
      return this.$store.state.currentFolder;
    },
    subfolders(): WorkspaceFolderModel[] {
      if (this.currentFolder) {
        return this.currentFolder.children.filter((object) => object.metadata.type === WorkspaceObjectType.Folder) as WorkspaceFolderModel[];
      }
      return [];
    },
  },
  methods: {
    toggleSelection(item: WorkspaceFolderModel) {
      this.$emit('toggle', item);
    },
    visitFolder(folderId: string) {
      if (!this.selectModeOpen) {
        const currentFolderId = this.$store.state.currentFolder.uuid;
        if (folderId !== currentFolderId) {
        // change params.folderId to switch folder
          this.$router.push({
            params: {
              folderId,
            },
          });
        }
      }
    },
  },
});
